<template>
    <div>
        <div :class="{'bianhui' : tanchu}">
            <div class="jjheader">
                <div class="userjj" >
                    <img :src="thisjj.userimg" alt="">
                    <div>
                        <p>
                            {{thisjj.username}}
                        </p>
                        <p>
                            {{thisjj.date}}
                        </p>
                    </div>
                </div>
                <div class="guanzhu">
                    <button>关注</button>
                </div>
            </div>
            <div class="jjmain">
                <p>
                    {{thisjj.tit}}
                </p>
                <img :src="thisjj.img" alt="" style="width: 100%">
                <div class="mian-main" v-html="thisjj.zhuti">
                </div>
            </div>
            <div class="jjmain2">
                <div class="main2-top">
                    <div class="main2-tit">风味菜谱</div>
                </div>
                <p class="caidanname">
                    {{thisjj.cainame}}
                </p>
                <div class="caipu">
                    <p v-show="thisjj.yuanliao">
                        <span>原料</span>: <span>{{thisjj.yuanliao}}</span>
                    </p>
                    <p v-show="thisjj.tiaoliao">
                        <span>调料</span>: <span>{{thisjj.tiaoliao}}</span>
                    </p>
                </div>
                <div class="buzhou">
                    <p><span>步骤</span>:</p>
                    <p v-for="(buzhou,a) in thisjj.buzhou" :key="a">{{a+1}}、{{buzhou}}。</p>

                </div>
                <div class="main2-bottom">
                    <div>
                        <img src="/img/chihu/xiangqing/dianzan.png" alt="" v-show="!dz" @click="dz=true,caozuo.dianzan+=1">
                        <img src="/img/chihu/xiangqing/dianzan-on.png" alt="" v-show="dz" @click="dz=false,caozuo.dianzan-=1">
                        {{caozuo.dianzan}}
                    </div>
                    <div @click="pinglun()">
                        <img src="/img/chihu/xiangqing/pinglun.png" alt="">{{plnum}}
                    </div>
                    <div>
                        <img src="/img/chihu/xiangqing/shoucang.png" alt="" v-show="!sc" @click="sc=true,caozuo.shoucang+=1">
                        <img src="/img/chihu/xiangqing/shoucang-on.png" alt="" v-show="sc" @click="sc=false,caozuo.shoucang-=1">
                        {{caozuo.shoucang}}
                    </div>
                </div>
            </div>
            <div class="jjbottom">
                <div class="bottom-main">
                    <div @click="fh()">
                        <img src="/img/chihu/xiangqing/fanhui.png" alt="">
                    </div>
                    <div>
                        <button>一键购买所有食材</button>
                    </div>
                    <div @click="fenxiang()">
                        <img src="/img/chihu/xiangqing/zhuanfa.png" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="tanchuceng" v-show="tanchu">
            <p>分享到</p>
            <div class="zhuafa">
                <div>
                    <img src="/img/chihu/xiangqing/weixin.png" alt="">
                    <p>微信好友</p>
                </div>
                <div>
                    <img src="/img/chihu/xiangqing/pengyouquan.png" alt="">
                    <p>朋友圈</p>
                </div>
            </div>
            <div class="quxiao" @click="quxiao()">
                取消
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "chihujj",
        data(){
          return{
              thisjj:{},
              jieshao:{},
            caozuo:{
                "dianzan": 0,
                "pinglun": [],
                "shoucang": 0
            },
              plnum:0,
              dz: false,
              sc: false,
              tanchu: false
          }
        },
        props:{
            id: Number
        },
        created() {
            //根据ID获取数据
            this.$axios.get("/json/chihujianjie.json").then(res=> {
                for (var i = 0; i < res.data.length; i++) {
                    if (this.$route.query.id == res.data[i].id) {
                        // console.log("123")
                        this.thisjj = res.data[i]
                    }
                }
            })

            this.$axios.get("/json/chihucaozuo.json").then(res=>{
                for (var i=0;i<res.data.length;i++){
                    if(this.$route.query.id == res.data[i].id){
                        this.caozuo = res.data[i]
                        this.plnum = res.data[i].pinglun.length
                    }
                }
            }).catch(err=>{
                console.log(err)
            })
        },
        methods:{
            pinglun(){
                if(this.plnum == 0){
                    alert("暂无评论！")
                }else {
                    this.$router.push({
                        path:"/pinglunqu",
                        query:{
                            id: this.$route.query.id
                        }
                    })
                }
            },
            fh(){
                this.$router.go(-1)
            },
            fenxiang(){
                this.tanchu = true
                //禁止页面滚动
                var mo=function(e){e.preventDefault()}
                document.body.style.overflow='hidden'
                document.addEventListener("touchmove",mo,false)
            },
            quxiao(){
                this.tanchu = false
                //取消禁止
                var mo=function(e){e.preventDefault()}
                document.body.style.overflow=''
                document.addEventListener("touchmove",mo,false)
            }
        //   当点击微信 、朋友圈进行转发时也要去掉锁定
        }
    }
</script>

<style scoped>
