        justify-content: space-between;
    }
    .jjheader .userjj{
        display: flex;
        justify-content: left;
    }
    .jjheader .userjj>img{
        margin-top: 15px;
        height: 60px;
        width: 60px;
        border-radius: 50%;
        overflow: hidden;
    }
    .jjheader .userjj>div{
        margin-left: 10px;
        margin-top: 10px;
        display: inline-block;
        height: 60px;
    }
    .jjheader .userjj>div>p:first-child{
        font-size: 24px;
