        line-height: 40px;
        padding-bottom: 15px;
        padding-top: 10px;
    }
    .jjmain .mian-main>>>img{
        width: 100%;
    }
    .jjmain .mian-main{
        margin-bottom: 60px;
    }

    .jjmain2{
        width: 100%;
        padding-left: 30px;
        padding-right: 30px;
    }
    .jjmain2 .main2-top{
        text-align: center;
    }
    .jjmain2 .main2-top .main2-tit{
        display: inline-block;
        height: 50px;
